/*////*/
.WrapperStepForm{
    background: #FFFFFF;
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.12);
    border-radius: 8px;
    padding: 40px 20px;
    max-width: 460px;
    margin: 0 auto;
}


.WrapperStepFormItems:not(:last-child){
    margin-bottom: 20px;
}

/*////*/

.form-divider{
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
    display: flex;
}

.form-divider:after{
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    height: 1px;
    width: 100%;
    background: var(--ccc);
    margin-top: -0.5px;
    z-index: -1;
}

.form-divider__text{
    font-weight: 300;
    font-size: 14px;
    background: var(--text-white-color);
    color: var(--707070);
    padding: 2px 16px;
    display: inline-block;
}

