.Button{
    font-family: Nunito, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;

    height: 40px;
    padding: 0 30px;
    background: var(--147AA9);
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    border: none;
    color: var(--text-white-color);
    width: 100%;
}

