.Sidebar{
    background: var(--text-white-color);
    width: 68px;
    padding: 22px 12px;
    height: 100%;
}

.Sidebar_open{
    width: 220px;
}

.Sidebar_right{
    position: relative;
}
.Sidebar_btn{
    content: '';
    position: absolute;
    right: 100%;
    top: 50%;
    margin-top: -50px;
    margin-right: -1px;
    width: 50px;
    height: 100px;
    background: url("../../assets/images/icons/arrow-sidebar.svg") no-repeat center right;
    background-size: contain;
    cursor: pointer;
}