
.Header{
    color: var(--text-white-color);
    background: var(--blue-color);
    position: relative;
    z-index: 100;
}

.Header__inner{
    display: flex;
    align-items: center;
    height: 60px;
    justify-content: space-between;
}

.Header__inner_center{
    justify-content: center;
}

.Header__items{
    display: flex;
    align-items: center;
    height: 100%;
    gap: 20px;
}
.Header__items_no-gap{
    gap: 0;
}
.Header__controls{
    height: 100%;
    display: flex;
    align-items: center;
}
.Header__controls-item{
    border-left: 2px solid var(--dark-blue-color);
    padding: 0 15px;
    height: 100%;
    display: flex;
    align-items: center;
}
.Header__controls-item:last-child{
    border-right: 2px solid var(--dark-blue-color);
}

.logo{
    font-size: 30px;
    font-weight: bold;
    font-family: Nunito, sans-serif;

    text-decoration: none;
    color: var(--text-white-color);
}

.Header__burger{
    margin-left: -15px;
}