.ItemWithIcon{
    display: flex;
    align-items: center;
    gap: 10px;
    cursor: pointer;
}
.ItemWithIcon:hover .ItemWithIcon__label{
    color: var(--dark-blue-color);
}
.ItemWithIcon__label{
    color: var(--text-black-color);
    font-weight: 600;
    font-size: 15px;
    text-transform: capitalize;
}
.ItemWithIcon__img-wrapper{
    width: 24px;
}
.Link-direction-no{
    text-decoration: none;
}