.SettingTable{
    background: var(--text-white-color);
    padding: 30px 30px;
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.12);
    border-radius: 8px;
    max-width: 670px;
}

.SettingTable__title{
    font-weight: 600;
    font-size: 18px;
    color: #525252;
    margin-bottom: 25px;
}

.SettingTable__column:not(:last-child){
    margin-bottom: 30px;
}

.SettingTable__items{
    display: flex;
    gap: 20px;
}
.SettingTable__item{
    width: calc(50% - 10px);
}