.ProfileTable{
    background: var(--text-white-color);
    padding: 30px 30px;
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.12);
    border-radius: 8px;

    display: flex;
    gap: 20px;
}

.ProfileTable__form-wrapper{
    flex: 1 1 auto;
}

/*---*/

.ProfileTable-form{

}

/*----*/

.ProfileTable-avatar{
    display: flex;
    flex-direction: column;
    gap: 12px;

    width: 160px;
}
.ProfileTable-avatar__wrapper{
    width: 160px;
    height: 160px;
}
.ProfileTable-avatar__img{
    object-fit: cover;
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 4px;
}

.ProfileTable-download{
    display: flex;
    gap: 10px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.ProfileTable-download__text{
    font-size: 15px;
    line-height: 1;
    color: #147AA9;
}