@import url(https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200;0,300;0,400;0,600;0,700;0,800;0,900;1,200;1,300;1,400;1,600;1,700;1,800;1,900&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
/* my */
*{
    box-sizing: border-box;
}
img{
    max-width: 100%;
}
:root {
    --gray-color: #F6F9FB;
    --blue-color: #147AA9;
    --dark-blue-color: #0B629C;
    --green-color: #80ED99;
    --dark-green-color: #57CC99;
    --red-color: #E71717;

    --text-black-color: #333333;
    --text-white-color: #fff;
    --text-dark-color: #525252;

    --707070: #707070;
    --ccc: #ccc;
    --147AA9: #147AA9;
    --333: #333333;
    --525252: #525252;
}


.page{
    overflow: hidden;
    font-family: 'Nunito', sans-serif;
    color: #333333;
    color: var(--text-black-color);
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background: #F6F9FB;
    background: var(--gray-color);
}

._container{
    margin: 0 auto;
    padding: 0 15px;
}

.Header{
    color: var(--text-white-color);
    background: var(--blue-color);
    position: relative;
    z-index: 100;
}

.Header__inner{
    display: flex;
    align-items: center;
    height: 60px;
    justify-content: space-between;
}

.Header__inner_center{
    justify-content: center;
}

.Header__items{
    display: flex;
    align-items: center;
    height: 100%;
    grid-gap: 20px;
    gap: 20px;
}
.Header__items_no-gap{
    grid-gap: 0;
    gap: 0;
}
.Header__controls{
    height: 100%;
    display: flex;
    align-items: center;
}
.Header__controls-item{
    border-left: 2px solid var(--dark-blue-color);
    padding: 0 15px;
    height: 100%;
    display: flex;
    align-items: center;
}
.Header__controls-item:last-child{
    border-right: 2px solid var(--dark-blue-color);
}

.logo{
    font-size: 30px;
    font-weight: bold;
    font-family: Nunito, sans-serif;

    text-decoration: none;
    color: var(--text-white-color);
}

.Header__burger{
    margin-left: -15px;
}

.Avatar{
    position: relative;
}
.Avatar__inner{
    display: flex;
    align-items: center;
    grid-gap: 15px;
    gap: 15px;
    cursor: pointer;
}

.Avatar__img-wrapper{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    border: 1px solid var(--text-white-color);
    flex: 0 0 auto;
}

.Avatar__img{
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.Avatar__text{
    font-size: 16px;
    font-weight: 500;
}

.Avatar__popUp{
    position: absolute;
    top: 100%;
    right: calc(100% - 66px);
    display: block;
    margin-top: 11px;
}
.PopUp{
    background: var(--text-white-color);
    padding: 20px;
    min-width: 130px;
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.12);
    border-radius: 4px;
}

.PopUp:after{
    content: '';
    position: absolute;
    bottom: 100%;
    right: 15px;
    border: 10px solid transparent;
    border-bottom: 10px solid var(--text-white-color);
}
.List__item:not(:last-child){
    margin-bottom: 15px;
}
.ItemWithIcon{
    display: flex;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    cursor: pointer;
}
.ItemWithIcon:hover .ItemWithIcon__label{
    color: var(--dark-blue-color);
}
.ItemWithIcon__label{
    color: var(--text-black-color);
    font-weight: 600;
    font-size: 15px;
    text-transform: capitalize;
}
.ItemWithIcon__img-wrapper{
    width: 24px;
}
.Link-direction-no{
    text-decoration: none;
}
.Languages{
    position: relative;
}

.Languages__btn{
    width: 24px;
    height: 24px;
    cursor: pointer;
}

.Languages__popUp{
    position: absolute;
    top: 100%;
    right: calc(100% - 36px);
    display: block;
    margin-top: 18px;
}

.Notification__inner{
    cursor: pointer;
    position: relative;
}

.Notification__inner_active:after{
    content: '';
    position: absolute;
    top: 4px;
    right: 4px;
    background: var(--red-color);
    width: 6px;
    height: 6px;
    border-radius: 50%;
}
.MenuBtn{
    text-align: center;
    width: 68px;
}
.MenuBtn__btn{
    cursor: pointer;
}
.Main{
    position: relative;
    min-height: calc(100vh - 60px);
    display: flex;
    justify-content: space-between;
}

.Main__content{
    padding: 30px 0 70px;
    flex: 1 1 auto;
}

.Main__sidebar{
}
.Main__left-sidebar{

}
.Main__right-sidebar{
}
.Sidebar{
    background: var(--text-white-color);
    width: 68px;
    padding: 22px 12px;
    height: 100%;
}

.Sidebar_open{
    width: 220px;
}

.Sidebar_right{
    position: relative;
}
.Sidebar_btn{
    content: '';
    position: absolute;
    right: 100%;
    top: 50%;
    margin-top: -50px;
    margin-right: -1px;
    width: 50px;
    height: 100px;
    background: url(/static/media/arrow-sidebar.e656a4f2.svg) no-repeat center right;
    background-size: contain;
    cursor: pointer;
}
.MenuSidebar{

}

.MenuSidebar__head{
    cursor: pointer;
}
.MenuSidebar__head:hover .MenuSidebar__head-img-wrapper{
    background: var(--dark-blue-color);
}
.MenuSidebar__head:hover .MenuSidebar__head-img {
    fill: var(--text-white-color);
}
.MenuSidebar__head-img{
    fill: var(--text-black-color);
}

.MenuSidebar__head-img-wrapper{
    border-radius: 4px;
    width: 44px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.MenuSidebar__item:not(:last-child){
    margin-bottom: 15px;
}
.PageTitle{
    margin-bottom: 30px;
}

.PageTitle__text{
    color: var(--text-dark-color);
    font-weight: 600;
    font-size: 24px;
    line-height: 33px;
}
.ProfileTable{
    background: var(--text-white-color);
    padding: 30px 30px;
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.12);
    border-radius: 8px;

    display: flex;
    grid-gap: 20px;
    gap: 20px;
}

.ProfileTable__form-wrapper{
    flex: 1 1 auto;
}

/*---*/

.ProfileTable-form{

}

/*----*/

.ProfileTable-avatar{
    display: flex;
    flex-direction: column;
    grid-gap: 12px;
    gap: 12px;

    width: 160px;
}
.ProfileTable-avatar__wrapper{
    width: 160px;
    height: 160px;
}
.ProfileTable-avatar__img{
    object-fit: cover;
    width: 100%;
    height: 100%;
    display: block;
    border-radius: 4px;
}

.ProfileTable-download{
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
.ProfileTable-download__text{
    font-size: 15px;
    line-height: 1;
    color: #147AA9;
}
.ProfileTableForm{
    display: flex;
    flex-direction: column;
    grid-gap:20px;
    gap:20px;
}

.ProfileTableForm__column{
    display: flex;
    grid-gap: 20px;
    gap: 20px;
}

.ProfileTableForm__item{
    width: calc(33.333% - 13.333px);
}
.Label{
    color: var(--707070);
    font-weight: 300;
    font-size: 15px;
}
.FormGroup{
    display: flex;
    flex-direction: column;
    grid-gap: 8px;
    gap: 8px;
}
.Input{
    font-family: Nunito;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    color: var(--333);

    background: #FFFFFF;
    border: 1px solid var(--ccc);
    border-radius: 4px;
    outline: none;
    padding: 0 12px;
    height: 40px;
    width: 100%;
}
.Button{
    font-family: Nunito, sans-serif;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;

    height: 40px;
    padding: 0 30px;
    background: var(--147AA9);
    border-radius: 4px;
    outline: none;
    cursor: pointer;
    border: none;
    color: var(--text-white-color);
    width: 100%;
}


.DataPicker{

}

.DataPicker__input{
    font-family: Nunito;
    font-style: normal;
    font-weight: 300;
    font-size: 16px;
    color: var(--333);

    background: #FFFFFF;
    border: 1px solid var(--ccc);
    border-radius: 4px;
    outline: none;
    padding: 0 12px;
    height: 40px;
    width: 100%;
}
.RegisterConf{
    font-weight: 300;
    font-size: 14px;
    line-height: 19px;
    text-align: center;
    color: var(--333);
}

.RegisterConf__link{
    font-size: 14px;
    color: var(--147AA9);
}
.SettingTable{
    background: var(--text-white-color);
    padding: 30px 30px;
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.12);
    border-radius: 8px;
    max-width: 670px;
}

.SettingTable__title{
    font-weight: 600;
    font-size: 18px;
    color: #525252;
    margin-bottom: 25px;
}

.SettingTable__column:not(:last-child){
    margin-bottom: 30px;
}

.SettingTable__items{
    display: flex;
    grid-gap: 20px;
    gap: 20px;
}
.SettingTable__item{
    width: calc(50% - 10px);
}
.Login{

}

/*////*/
.WrapperStepForm{
    background: #FFFFFF;
    box-shadow: 0px 3px 12px rgba(0, 0, 0, 0.12);
    border-radius: 8px;
    padding: 40px 20px;
    max-width: 460px;
    margin: 0 auto;
}


.WrapperStepFormItems:not(:last-child){
    margin-bottom: 20px;
}

/*////*/

.form-divider{
    justify-content: center;
    align-items: center;
    position: relative;
    z-index: 1;
    display: flex;
}

.form-divider:after{
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    height: 1px;
    width: 100%;
    background: var(--ccc);
    margin-top: -0.5px;
    z-index: -1;
}

.form-divider__text{
    font-weight: 300;
    font-size: 14px;
    background: var(--text-white-color);
    color: var(--707070);
    padding: 2px 16px;
    display: inline-block;
}

/*///*/

.WrapperStepForm{

}

/*////*/
.WrapperStepFormTitle{
    font-weight: 600;
    font-size: 24px;
    text-align: center;
    color: #525252;
    margin-bottom: 30px;
}

/*/*/

.ConfirmMail__img{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 30px;
}

.ConfirmMail__text{
    font-weight: 300;
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: var(--333);
}
.LogInSocial{
    text-align: center;
}

.LogInSocial__title{
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: var(--525252);
}

.LogInSocial__maybe{
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: var(--147AA9);
    text-decoration: none;
}

.LogInSocial__items{
    display: flex;
    grid-gap: 20px;
    gap: 20px;
    justify-content: center;
    margin: 22px 0;
}

.facebook{
    background: #3B5998;
}

.twitter{
    background: #55ACEE;
}

.google{
    background: white;
}

.facebook,
.google,
.twitter{
    height: 40px;
    width: 40px;
    cursor: pointer;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

    box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.24);
}

