.LogInSocial{
    text-align: center;
}

.LogInSocial__title{
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
    color: var(--525252);
}

.LogInSocial__maybe{
    font-size: 16px;
    line-height: 22px;
    text-align: center;
    color: var(--147AA9);
    text-decoration: none;
}

.LogInSocial__items{
    display: flex;
    gap: 20px;
    justify-content: center;
    margin: 22px 0;
}

.facebook{
    background: #3B5998;
}

.twitter{
    background: #55ACEE;
}

.google{
    background: white;
}

.facebook,
.google,
.twitter{
    height: 40px;
    width: 40px;
    cursor: pointer;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;

    box-shadow: 0px 1px 6px rgba(0, 0, 0, 0.24);
}
