.MenuSidebar{

}

.MenuSidebar__head{
    cursor: pointer;
}
.MenuSidebar__head:hover .MenuSidebar__head-img-wrapper{
    background: var(--dark-blue-color);
}
.MenuSidebar__head:hover .MenuSidebar__head-img {
    fill: var(--text-white-color);
}
.MenuSidebar__head-img{
    fill: var(--text-black-color);
}

.MenuSidebar__head-img-wrapper{
    border-radius: 4px;
    width: 44px;
    height: 44px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.MenuSidebar__item:not(:last-child){
    margin-bottom: 15px;
}