
.Avatar{
    position: relative;
}
.Avatar__inner{
    display: flex;
    align-items: center;
    gap: 15px;
    cursor: pointer;
}

.Avatar__img-wrapper{
    width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
    border: 1px solid var(--text-white-color);
    flex: 0 0 auto;
}

.Avatar__img{
    object-fit: cover;
    width: 100%;
    height: 100%;
}

.Avatar__text{
    font-size: 16px;
    font-weight: 500;
}

.Avatar__popUp{
    position: absolute;
    top: 100%;
    right: calc(100% - 66px);
    display: block;
    margin-top: 11px;
}